@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');

*{
  margin: 0;
  padding: 0;

}

body {
  background: #fff;
  color: #222;
  font-family: "Montserrat", serif;
  font-weight: 300;
}

.wrapper {
  width: 1280 px;
  margin: 50px 200px;

}

header{
  position: relative;
}

header .logo{
  font-weight: 600;
  font-size: 20px;
}

header ul.nav{
  float: right;
  list-style: none;

}

header ul.nav li{
  display: inline;
  margin-left: 25px;
  cursor:pointer;
  transition: opasity 500ms ease;

}

header ul.nav li:hover {
  opacity: 0.5;

}

header .shop-cart-button{
  float: right;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart-button:hover,
header .shop-cart-button.active{
  color: #dc3d3d;
  transform: scale(1.5);
}

header .shop-cart{
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 4px 5px 15px -7px #606060;
  box-shadow: 4px 5px 15px -7px #606060;
  z-index:1000;

  padding: 20px;
  padding-bottom: 0;
}

header .shop-cart .object{
  width: 100%;
  float: left;
  margin-bottom: 20px;


}

header .shop-cart .object .delete-icon{
  color: #ca5252;
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}

header .shop-cart .object .delete-icon:hover{
  color: #d91414;
  transform: scale(1.5);
}

header .shop-cart .empty h2{
  font-size: 20px;
  margin-bottom: 20px;
}

header .shop-cart .object img{
  width: 70px;
  float:left;
  margin-right: 20px;

}
header .shop-cart .object b{
  color:#797979;
  font-weight: 600;

}

header .shop-cart .object h2{
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-cart .summa{
  float: left;
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
}

header .presentation{
  margin: 50px 0;
  background: url("img/1.JPG") no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;

}

header .presentation::after{
  content: 'New collection is coming..';
  position: absolute;
  top: 100px;
  left: 50px;
  color: #fff;
  font-size: 40px;
  font-weight: 600;
  width: 325px;
}

header .presentation::before{
  content: 'best outfit shop';
  position: absolute;
  top: 220px;
  left: 50px;
  color: #d717a4;
  font-size: 15px;
  font-weight: 600;
  width: 325px;
}

.categories div{
  display: inline-block;
  background: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin-bottom:25px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}

.categories div:hover{
  border-color: silver;
  transform: scale(1.1);
}

.full-object{
  position:fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  overflow: auto;
}

.full-object >div{
  width: 600px;
  position: relative;
  margin:10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}

main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}

main .object{
  width: 30%;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
  filter: brightness(1.1);
}

main .object img,
.full-object img{
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}
main .object img:hover,
.full-object img:hover{
  transform: scale(1.05);
}

main h2,main p,main b, .full-object h2, .full-object p, .full-object b{
  margin: 10px 20px;
  color: #333;
  margin-left: 10%;
}

main b, .full-object b{
  color:#27a53c;
}

main .add-to-cart,
.full-object .add-to-cart{
  position: absolute;
  right: 20px;
  bottom: 20px;
  background-color: #ca5252;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  color: #fff;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}

main .add-to-cart:hover,
.full-object.add-to-cart:hover
{
  transform: scale(1.5) translateY(-5px);
}







footer{
  text-align: center;
  margin-top: 100px;
}

